"use client";

import * as React from "react";
import { Loader2 } from "lucide-react";
import axiosClient from "@/lib/axiosClient";
import Preview from "@/app/preview/Preview";

type PreviewPayload = {
  templateName: string;
  sections: {
    section: string;
    content: Record<string, any>;
  }[];
  styles: Record<string, any>;
};

interface Props {
  productId?: number;
}

export default function LandingPageBuilder({ productId = 32 }: Props) {
  const [isLoading, setIsLoading] = React.useState<boolean>(true);
  const [payload, setPayload] = React.useState<PreviewPayload | null>(null);

  React.useEffect(() => {
    if (!productId) {
      setIsLoading(false);
      return;
    }

    const fetchPreviewData = async () => {
      try {
        setIsLoading(true);

        const res = await axiosClient.get(
          `/products/${productId}/landing-page`
        );

        if (res.data?.success && res.data?.data?.sections?.length) {
          const activeSections = res.data.data.sections
            .filter((sec: any) => sec.is_published)
            .sort((a: any, b: any) => a.sort_order - b.sort_order);

          setPayload({
            templateName: "modern",
            sections: activeSections.map((sec: any) => ({
              section: sec.global_section.key,
              content: sec.content ?? {},
            })),
            styles: {
              primaryColor: "#ecedf4ff",
              backgroundColor: "#000000",
              textColor: "#a97d38",
              headlineSize: 28,
              paragraphSize: 16,
            },
          });
        } else {
          setPayload({
            templateName: "modern",
            sections: [
              {
                section: "hero",
                content: {
                  title: "No Sections Published",
                  subtitle:
                    "Enable sections in the builder to see them here.",
                },
              },
            ],
            styles: {
              primaryColor: "#ecedf4ff",
              backgroundColor: "#000000",
              textColor: "#a97d38",
              headlineSize: 28,
              paragraphSize: 16,
            },
          });
        }
      } catch (error) {
        console.error("Failed to fetch landing page data", error);

        setPayload({
          templateName: "modern",
          sections: [
            {
              section: "hero",
              content: {
                title: "Error Loading Preview",
                subtitle: "Check console for details.",
              },
            },
          ],
          styles: {
            primaryColor: "#ecedf4ff",
            backgroundColor: "#000000",
            textColor: "#a97d38",
            headlineSize: 28,
            paragraphSize: 16,
          },
        });
      } finally {
        setIsLoading(false);
      }
    };

    fetchPreviewData();
  }, [productId]);

  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-screen">
        <Loader2 className="h-12 w-12 animate-spin" />
        <p className="ml-4 text-lg">Loading preview data...</p>
      </div>
    );
  }

  if (!payload) {
    return (
      <div className="flex items-center justify-center h-screen">
        <p className="text-lg">No preview data available.</p>
      </div>
    );
  }

  return <Preview data={payload} productId={productId} />;
}
